<template>
    <section>
        <div
            v-if="!shouldAutosave"
            class="mb-8 flex justify-around"
        >
            <button
                class="btn btn--primary"
                @click="saveStateToStorage"
            >
                Save
            </button>
        </div>

        <div class="flex justify-around items-start">
            <div class="w-1/2 flex flex-col items-start">
                <label class="mb-4">
                    <button
                        class="btn btn--secondary"
                        @click="$refs.fileInput.click()"
                    >
                        Upload a data/settings file that's been previously exported
                    </button>
                    <input
                        ref="fileInput"
                        type="file"
                        class="hidden"
                        accept="application/json"
                        @change="onFileUpload"
                    />
                </label>

                <div
                    v-if="fileToImportIsValid"
                    class="mb-4 flex flex-col items-start"
                >
                    <label>
                        Import data
                        <input
                            v-model="shouldImportData"
                            class="ml-4"
                            type="checkbox"
                        />
                    </label>

                    <label>
                        Import settings
                        <input
                            v-model="shouldImportSettings"
                            class="ml-4"
                            type="checkbox"
                        />
                    </label>

                    <p>
                        <sub
                            v-if="!shouldImportData && !shouldImportSettings"
                            class="text-gray-500"
                        >
                            (you need to import data and/or settings)
                        </sub>
                    </p>

                    <label v-if="shouldImportSettings">
                        If the settings I'm importing will start backing up data to Firebase (and I wasn't already),
                        <select
                            v-model="shouldTakeDataFrom"
                            class="select text-red"
                        >
                            <option value="local">
                                keep using the data that's been stored locally
                            </option>
                            <option value="firebase">
                                use any data that's been stored in Firebase, and overwrite what I've stored locally
                            </option>
                        </select>
                    </label>
                    <label v-if="shouldImportData && shouldTakeDataFrom === 'local'">
                        If my settings will back up data to Firebase (and I'm not overwriting the locally-stored data with what's in Firebase),
                        <select
                            v-model="shouldSaveNewlyImportedDataToFirebase"
                            class="select text-red"
                        >
                            <option :value="true">
                                I want to
                            </option>
                            <option :value="false">
                                I don't want
                            </option>
                        </select>
                        to immediately save any newly-imported data to Firebase
                    </label>

                    <p>
                        <sub class="text-gray-500">
                            The file you're importing must look like this:
                        </sub>
                    </p>

                    <pre
                        class="overflow-y-auto"
                        style="max-height: 33vh"
                    ><code>{
    "postsModule": {
        "posts": {},
        "links": {},
        "graphs": {
            "1": {
                "id": 1,
                "name": "default",
                "nodes": [],
                "colour": "black"
            }
        },
        "selectedPostIds": [],
        "selectedGraphIds": []
    },
    "settingsModule": {
        "shouldAutosave": true,
        "remoteStorageMethod": "firebase",
        "canOpenMultiplePosts": true,
        "graphHeight": 66,
        "postBarHeight": 66
    },
    "firebaseModule": {
        "firebaseConfig": {
            "apiKey": "x",
            "authDomain": "y",
            "databaseURL": "z",
            "projectId": "xy",
            "storageBucket": "yz",
            "messagingSenderId": "xyz",
            "appId": "xx"
        }
    }
}</code></pre>
                </div>

                <button
                    class="btn btn--primary"
                    :disabled="!fileToImportIsValid || (!shouldImportData && !shouldImportSettings)"
                    @click="importState"
                >
                    Import the uploaded file
                </button>
                <sub
                    v-if="fileToImport !== null && (!fileToImportIsValid || (!shouldImportData && !shouldImportSettings))"
                    class="my-2 text-xs text-gray-500"
                >
                    {{ !fileToImportIsValid ? 'the file you\'ve uploaded isn\'t valid' : ((!shouldImportData && !shouldImportSettings) ? 'you must click one of the \'import\' buttons above' : '') }}
                </sub>
            </div>
            <div class="w-1/2">
                <button
                    class="btn btn--primary"
                    @click="exportState"
                >
                    Export data and settings
                </button>
            </div>
        </div>
    </section>
</template>

<script>
import moment from "moment";
import {mapState, mapGetters, mapActions} from "vuex";

export default {
    name: "LoadSave",
    data() {
        return {
            fileToImport: null,
            shouldImportData: false,
            shouldImportSettings: false,

            shouldTakeDataFrom: "local", // "local" | "firebase"
            shouldSaveNewlyImportedDataToFirebase: true,
        };
    },
    computed: {
        ...mapState("settingsModule", ["shouldAutosave"]),

        ...mapGetters(["storageObject"]),

        fileToImportIsValid() {
            return this.fileToImport !== null
                && this.fileToImport.type === "application/json";
        }
    },
    methods: {
        ...mapActions(["saveStateToLocalStorage", "saveStateToStorage", "loadStateFromStorage", "importData", "importSettings"]),

        onFileUpload(event) {
            const files = event.target.files || event.dataTransfer.files;
            if (!files.length || files.length > 1) {
                return;
            }
            const file = files[0];
            this.fileToImport = files[0];
            if (file.type !== "application/json") {
                alert("You must upload a JSON file exported by the 'export' button");
                return;
            }
        },
        async importState() {
            const stateString = await this.fileToImport.text();
            const parsedState = JSON.parse(stateString);

            if (
                !("postsModule" in parsedState)
                || !("settingsModule" in parsedState)
                || !("firebaseModule" in parsedState)
            ) {
                alert("Imported file isn't valid");
                return;
            }

            if (this.shouldImportData) {
                await this.importData(parsedState);
            }
            if (this.shouldImportSettings) {
                if (!this.shouldImportData) { // if you're importing the data as well as the settings, you obviously want to use the imported data
                    await this.importSettings({
                        storageObject: parsedState,
                        shouldTakeDataFrom: this.shouldTakeDataFrom
                    });
                } else {
                    await this.importSettings({
                        storageObject: parsedState,
                        shouldTakeDataFrom: null
                    });
                }
            }
            this.fileToImport = null;

            if (this.shouldSaveNewlyImportedDataToFirebase) {
                await this.saveStateToStorage();
            } else {
                await this.saveStateToLocalStorage();
            }
        },

        exportState() {
            const blob = new Blob(
                [JSON.stringify(this.storageObject)],
                {type: "application/json"}
            );
            const now = moment().format("Y-MM-DD_HH:mm:ss");

            this.downloadData(blob, `spiderwebExport-${now}.json`);
        },
        downloadData(blob, filename) {
            let a = document.createElement("a");
            document.body.appendChild(a);
            a.style = "display: none";

            const url = window.URL.createObjectURL(blob);
            a.href = url;
            a.download = filename;
            a.click();
            window.URL.revokeObjectURL(url);
        }
    }
};
</script>